<template>
  <div id="app">
    <div class="header_main">
      <header-nav></header-nav>
    </div>
    <div class="content">
      <router-view></router-view>
    </div>
    <div class="footer_main">
      <footer-box></footer-box>
    </div>
  </div>
</template>

<script>
import HeaderNav from '@/components/header-nav/header-nav'
import FooterBox from '@/components/footer-box/footer-box'
export default {
  name: 'App',
  components: {
    FooterBox,
    HeaderNav
  }
}
</script>

<style lang="scss" scoped>
  #app{
    position: relative;
  }
  .header_main{
    z-index: 999;
    position: fixed;
    top:0;
    left:0;
    width:100%;
  }
  .content{
    // padding-top: 162px;
    padding-bottom:420px;
  }
  .footer_main{
    width: 100%;
    position: absolute;
    bottom: 0;
  }
</style>



