<template>
  <div class="header_warp" :class="{scroll_warp: navShow == 1}">
    <!-- <b-navbar toggleable="md" type="dark" variant="info">
      <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>

      <b-navbar-brand href="#">NavBar</b-navbar-brand>

      <b-collapse is-nav id="nav_collapse">
        <b-navbar-nav class="ml-auto">
          <b-nav-item href="#">Link</b-nav-item>
          <b-nav-item href="#" disabled>Disabled</b-nav-item>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar> -->
    <b-navbar toggleable="md"  v-if="navShow == 0" class="header_nav">
      <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>

      <b-navbar-brand href="#">
        <img src="@/assets/img/head-nav/blue-logo.png" v-if="logoState == 'blue'" @click="chooseNav(0)">
        <img src="@/assets/img/head-nav/white-logo.png" v-if="logoState == 'white'" @click="chooseNav(0)">
      </b-navbar-brand>

      <b-collapse is-nav id="nav_collapse">
        <b-navbar-nav class="ml-auto blue_ul" v-if="navState == 1">
          <b-nav-item :class="{blue_active: state == 0}" @click="chooseNav(0)" class="list">首页</b-nav-item>
          <b-nav-item :class="{blue_active: state == 1}" @click="chooseNav(1)" class="list">案例</b-nav-item>
          <b-nav-item :class="{blue_active: state == 2}" @click="chooseNav(2)" class="list">联系方式</b-nav-item>
          <b-nav-item :class="{blue_active: state == 3}" @click="chooseNav(3)" class="list">人才招募</b-nav-item>
        </b-navbar-nav>
        <b-navbar-nav class="ml-auto white_ul" v-if="navState == 0">
          <b-nav-item :class="{white_active: state == 0}" @click="chooseNav(0)" class="list">首页</b-nav-item>
          <b-nav-item :class="{white_active: state == 1}" @click="chooseNav(1)" class="list">案例</b-nav-item>
          <b-nav-item :class="{white_active: state == 2}" @click="chooseNav(2)" class="list">联系方式</b-nav-item>
          <b-nav-item :class="{white_active: state == 3}" @click="chooseNav(3)" class="list">人才招募</b-nav-item>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>

    <b-navbar toggleable="md"  v-if="navShow == 1" class="scroll_nav header_nav">
      <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>

      <b-navbar-brand href="#">
        <img src="@/assets/img/head-nav/blue-logo.png" @click="chooseNav(0)">
      </b-navbar-brand>

      <b-collapse is-nav id="nav_collapse">
        <b-navbar-nav class="ml-auto list_ul">
          <b-nav-item :class="{blue_active: state == 0}" @click="chooseNav(0)" class="list">首页</b-nav-item>
          <b-nav-item :class="{blue_active: state == 1}" @click="chooseNav(1)" class="list">案例</b-nav-item>
          <b-nav-item :class="{blue_active: state == 2}" @click="chooseNav(2)" class="list">联系方式</b-nav-item>
          <b-nav-item :class="{blue_active: state == 3}" @click="chooseNav(3)" class="list">人才招募</b-nav-item>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>

    <!-- <div class="header_nav" v-if="navShow == 0">
      <img src="@/assets/img/head-nav/blue-logo.png" v-if="logoState == 'blue'">
      <img src="@/assets/img/head-nav/white-logo.png" v-if="logoState == 'white'">
      <ul class="blue_ul" v-if="navState == 1">
        <li :class="{blue_active: state == 0}" @click="chooseNav(0)">首页</li>
        <li :class="{blue_active: state == 1}" @click="chooseNav(1)">案例</li>
        <li :class="{blue_active: state == 2}" @click="chooseNav(2)">联系方式</li>
        <li :class="{blue_active: state == 3}" @click="chooseNav(3)">人才招募</li>
      </ul>
      <ul class="white_ul" v-if="navState == 0">
        <li :class="{white_active: state == 0}" @click="chooseNav(0)">首页</li>
        <li :class="{white_active: state == 1}" @click="chooseNav(1)">案例</li>
        <li :class="{white_active: state == 2}" @click="chooseNav(2)">联系方式</li>
        <li :class="{white_active: state == 3}" @click="chooseNav(3)">人才招募</li>
      </ul>
    </div>
    <div class="header_nav scroll_nav" v-if="navShow == 1">
      <img src="@/assets/img/head-nav/blue-logo.png">
      <ul>
        <li :class="{blue_active: state == 0}" @click="chooseNav(0)">首页</li>
        <li :class="{blue_active: state == 1}" @click="chooseNav(1)">案例</li>
        <li :class="{blue_active: state == 2}" @click="chooseNav(2)">联系方式</li>
        <li :class="{blue_active: state == 3}" @click="chooseNav(3)">人才招募</li>
      </ul>
    </div> -->
  </div>
</template>

<script>
export default {
  name: 'HeaderNav',
  data () {
    return {
      state: 0,
      navShow: 0,
      url: window.location.href,
      logoState: 'blue',
      navState: 0
    }
  },
  created () {
    this.jump()
  },
  methods: {
    chooseNav (num) {
      this.state = num
      if (num === 0) {
        this.$router.push({
          name: 'home'
        })
      } else if (num === 1) {
        this.$router.push({
          name: 'case'
        })
      } else if (num === 2) {
        this.$router.push({
          name: 'contact-way'
        })
      } else if (num === 3) {
        this.$router.push({
          name: 'join-us'
        })
      }
      this.url = window.location.href
      this.jump()
      window.scrollTo(0, 0)
    },
    menu () {
      this.scroll = document.documentElement.scrollTop || document.body.scrollTop
      if (this.scroll > 162) {
        this.navShow = 1
      } else {
        if (this.scroll === 0) {
          this.url = window.location.href
          this.jump()
        }
        this.navShow = 0
      }
    },
    jump () {
      if (this.url.indexOf('/home') > -1) {
        this.logoState = 'white'
        this.navState = 0
        this.state = 0
      } else if (this.url.indexOf('/case') > -1) {
        if (document.body.offsetWidth < 767) {
          this.logoState = 'blue'
        } else {
          this.logoState = 'white'
        }
        this.navState = 1
        this.state = 1
      } else if (this.url.indexOf('/contact-way') > -1) {
        this.logoState = 'blue'
        this.navState = 1
        this.state = 2
      } else {
        this.logoState = 'blue'
        this.navState = 1
        this.state = 3
      }
    }
  },
  mounted () {
    window.addEventListener('scroll', this.menu)
    window.scrollTo(0, 0)
  }
}
</script>

<style scoped>
@import "./header-nav.scss";
</style>



