<template>
    <div class="footer_box">
        <!-- 上部分 -->
        <div class="foot_top">
          <div class="company_title">
            <img src="~@/assets/img/head-nav/white-logo.png" alt="">
            <span>杭州点存区块链</span>
          </div>
          <div class="company_info">
            <p>
              <img src="~@/assets/img/footer/phone.png" alt="">
              <span>Tel： 0571-86203769 / 15356159698</span>
            </p>
            <p>
              <img src="~@/assets/img/footer/email.png" alt="">
              <span>Email： davidli898@163.com</span>
            </p>
            <p>
              <img src="~@/assets/img/footer/address.png" alt="">
              <span>Address： 浙江省杭州市余杭区文一西路与常二路交叉口富力天贸26幢818</span>
            </p>
          </div>
        </div>
        <!-- 下部分 -->
        <div class="foot_bottom">
          <b></b>
          <span @click="chooseNav(0)">首页</span>
          <b></b>
          <span @click="chooseNav(1)">案例</span>
          <b></b>
          <span @click="chooseNav(2)">联系方式</span>
          <b></b>
          <span @click="chooseNav(3)">人才招聘</span>
          <b></b>
        </div>
    </div>
</template>
<script>
export default {
  name: 'FooterBox',
  data () {
    return {
    }
  },
  methods: {
    chooseNav (num) {
      if (num === 0) {
        this.$router.push({
          name: 'home'
        })
      } else if (num === 1) {
        this.$router.push({
          name: 'case'
        })
      } else if (num === 2) {
        this.$router.push({
          name: 'contact-way'
        })
      } else if (num === 3) {
        this.$router.push({
          name: 'join-us'
        })
      }
      window.scrollTo(0, 0)
    }
  }
}
</script>

<style scoped>
  .foot_bottom{
    background: #1C2027;
    color:#cbcdd4;
    border-top:1px solid #3f434a;
    padding:20px 0;
    text-align: center;
  }
  .foot_bottom>b{
    width:1px;
    height:12px;
    display: inline-block;
    background: #cbcdd4;
  }
  .foot_bottom>span{
    padding:0 15px;
    cursor: pointer;
  }
  .foot_top{
    height:400px;
    background: url("~@/assets/img/footer/foot-bg.png") no-repeat;
    background-size: 100% 100%;
    color:#cbcdd4;
  }
  .company_title>img{
    height:50px;
    margin-right:20px;
  }
  .company_info>p{
    padding-top:28px;
  }
  .company_info>p>img{
    margin-right:16px;
  }
  @media (max-width: 767px) {
    .company_title{
      font-size: 16px;
      padding-top:100px;
      width:100%;
      text-align: center;
    }
    .company_info{
      font-size: 13px;
      padding-top:25px;
      width:100%;
      text-align: center;
    }
    .foot_bottom{
      font-size:12px;
    }
  }
  @media (min-width: 768px) and (max-width: 991px) {
    .company_title{
      font-size: 16px;
      padding-top:100px;
      width:100%;
      text-align: center;
    }
    .company_info{
      font-size: 13px;
      padding-top:20px;
      width:100%;
      text-align: center;
    }
    .foot_bottom{
      font-size:12px;
    }
  }
  @media (min-width: 992px) and (max-width: 1199px) {
    .company_title{
      font-size: 20px;
      padding-top:176px;
      width:60%;
      text-align: center;
      float: left;
    }
    .company_info{
      font-size: 16px;
      padding-top:100px;
      width:40%;
      float: left;
    }
    .foot_bottom{
      font-size:15px;
    }
   }
  @media (min-width: 1200px) {
    .company_title{
      font-size: 20px;
      padding-top:176px;
      width:60%;
      text-align: center;
      float: left;
    }
    .company_info{
      font-size: 16px;
      padding-top:100px;
      width:40%;
      float: left;
    }
    .foot_bottom{
      font-size:15px;
    }
  }
</style>



