import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/home/home'
import ContactWay from '@/pages/contact-way/contact-way'
import JoinUs from '@/pages/join-us/join-us'
import Case from '@/pages/case/case'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      redirect: 'home'
    }, {
      path: '/home',
      name: 'home',
      component: Home
    }, {
      path: '/contact-way',
      name: 'contact-way',
      component: ContactWay
    }, {
      path: '/join-us',
      name: 'join-us',
      component: JoinUs
    }, {
      path: '/case',
      name: 'case',
      component: Case
    }
  ]
})



