<template>
  <div class="m-map">
    <div id="js-container" class="map"></div>
  </div>
</template>

<script>
import remoteLoad from '@/utils/remoteLoad.js'
import { MapKey, MapCityName } from '@/config/config'
export default {
  props: ['lat', 'lng'],
  data () {
    return {
      placeSearch: null,
      dragStatus: false,
      AMapUI: null,
      AMap: null,
      map: undefined,
      marker: undefined
    }
  },
  methods: {
    // 实例化地图
    initMap () {
      let infoWindow
      let AMap = this.AMap = window.AMap
      console.log(AMap)
      let mapConfig = {
        resizeEnable: true,
        zoomEnable: false,
        zoom: 16,
        cityName: MapCityName
      }
      if (this.lat && this.lng) {
        mapConfig.center = [this.lng, this.lat]
      }
      let map = new AMap.Map('js-container', mapConfig)
      map.clearMap()
      let marker = new AMap.Marker({
        map: map
      })
      if (this.lat && this.lng) {
        marker.position = [this.lng, this.lat]
      }
      var info = []
      info.push('电话 : 0571-86203769 / 15356159698')
      info.push('邮编 : 310000')
      info.push('地址 :浙江省杭州市余杭区文一西路与常二路交叉口富力天贸26幢818</div></div>')
      infoWindow = new AMap.InfoWindow({
        content: info.join('<br/>'),
        offset: new AMap.Pixel(0, -20)
      })
      infoWindow.open(map, map.getCenter())
    }
  },
  async created () {
    // 已载入高德地图API，则直接初始化地图
    if (window.AMap && window.AMapUI) {
      this.initMap()
    // 未载入高德地图API，则先载入API再初始化
    } else {
      await remoteLoad(`https://webapi.amap.com/maps?v=1.3&key=${MapKey}`)
      await remoteLoad('https://webapi.amap.com/ui/1.0/main.js')
      this.initMap()
    }
  }
}
</script>

<style lang="css">
@media (max-width: 767px) {
  #js-container{width: 100%; min-height: 300px;margin: 0 auto;}
}
@media (min-width: 1202px) {
  #js-container{width: 100%; min-height: 500px;}
}
.m-map{ min-width: 100%; min-height: 300px; position: relative; }
.m-map .map{ width: 100%; height: 100%;}
</style>



